<template>
    
    <nav ref="refMainNav" class="main-nav no-print" @mouseenter="showNav" @mouseleave="closeNav">
        
        <div class="nav-bar">
            <button @click="isShow=!isShow" class="nav-bar-button"><span class="material-symbols-outlined">menu</span></button>
        </div>
        <div v-if="isShow" class="nav-menu">

            <ul class="btn-link-wrap">
                <li class="btn-link-item">
                    <router-link to="/">
                        <div class="image-wrap" :style="{'background-image': `url(${require('@/assets/images/toolbox.png')})`}"></div>
                        <span>主頁</span>
                    </router-link>
                </li>
            </ul>

            <ul v-if="false">
                <li>
                    <router-link to="/test">練習場</router-link>
                    <ul>
                        <li><router-link to="/test/todo-list">待辦事項</router-link></li>
                        <li><router-link to="/test/markdown-editor">markdown編輯器</router-link></li>
                        <li><router-link to="/test/name-component">操作資料練習</router-link></li>
                    </ul>
                </li>
            </ul>

            <!-- 公開專案 nav -->
            <PublicMenu />
        </div>

    </nav>

</template>
<script>
import { ref } from 'vue';

import PublicMenu from './PublicMenu.vue';

export default {
    name: 'BaseNavigation',
    components: {
        PublicMenu
    },

    setup() {
        // 數據
        let isShow = ref(false);

        // 元素
        const refMainNav = ref(null);


        function showNav() {
            // console.log('showNav');
            isShow.value = true;
        }
        function closeNav() {
            // console.log('closeNav');
            isShow.value = false;
        }

        

        return {
            isShow,
            refMainNav,

            showNav,
            closeNav,
        }
    }
}
</script>
